<template>
  <div class="error_log">
    <div class="topOpt">
      <el-form :inline="true" :model="form" label-width="80px">
        <el-form-item label="日志编号">
          <el-input v-model="form.log_code"></el-input>
        </el-form-item>
        <el-form-item label="日期">
          <el-date-picker
            v-model="form.dates"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="部门">
          <el-select
            @change="selectDepartment"
            v-model="form.department"
            placeholder="请选择"
          >
            <el-option
              v-for="item in workdepartments"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="编制人">
          <el-select v-model="form.organization_personnel" placeholder="请选择">
            <el-option
              v-for="item in personlist"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="主题搜索">
          <el-input v-model="form.theme"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="query">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_con">
      <h2>错误日志</h2>
      <el-button class="addNewBtn" type="primary" @click="newErrorLog"
        >新增</el-button
      >
      <el-table :data="tableData" style="width: 100%">
        <el-table-column align="center" type="index" label="序号" width="180">
        </el-table-column>
        <el-table-column
          align="center"
          prop="log_code"
          label="日志编号"
          width="180"
        >
        </el-table-column>
        <el-table-column align="center" prop="dates" label="日期">
        </el-table-column>
        <el-table-column
          align="center"
          prop="organization_personnel"
          label="编制人"
        >
        </el-table-column>
        <el-table-column align="center" prop="staff_code" label="员工编码">
        </el-table-column>
        <el-table-column align="center" prop="department" label="部门">
        </el-table-column>
        <el-table-column
          align="center"
          prop="theme"
          label="主题"
        ></el-table-column>
        <el-table-column align="center" label="查看">
          <template slot-scope="scope">
            <a href="javascript:;" @click="showDetail(scope.row)">查看详情</a>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      style="text-align:center;"
      :title="log_title"
      :visible.sync="isShowErrogDialog"
      width="30%"
    >
      <div>
        <!-- <h3>{{ log_title }}</h3> -->
        <div class="error_contain" style="padding: 0 113px;">
          <el-form :model="curSelectPerson" label-width="80px">
            <el-form-item label="部门">
              <el-select
                :disabled="!insertFlag"
                @change="changeDepartment"
                v-model="curSelectPerson.department"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in workdepartments"
                  :key="item"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="编制人">
              <el-select
                :disabled="!insertFlag"
                v-model="curSelectPerson.organization_personnel"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in personlist"
                  :key="item"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="主题搜索">
              <el-select
                v-model="curSelectPerson.theme"
                :disabled="!insertFlag"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in changeThemelist"
                  :key="item"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="内容">
              <el-input
                :disabled="!insertFlag"
                :rows="6"
                type="textarea"
                v-model="curSelectPerson.content"
              ></el-input>
            </el-form-item>

            <el-form-item v-if="insertFlag">
              <span class="uploadlink"
                ><a href="javascript:;" class="alink">附件上传</a>
                <input
                  ref="uploadInput"
                  @change="uploadFile"
                  type="file"
                  class="fileInput"
              /></span>
              <el-button type="primary" @click="onSubmit">提交</el-button>
            </el-form-item>
            <el-form-item v-if="curSelectPerson.files">
              <template>
                <span>文件名：{{ curSelectPerson.files }}</span>
              </template>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </el-dialog>

    <!-- <div
      class="new_error_log"
      :style="{
        'z-index': isShowErrogDialog ? '999' : '-999',
        opacity: isShowErrogDialog ? '1' : '0'
      }"
    >
      <span @click="closeErrorLog" class="close_btn">x</span>
    </div> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        log_code: "",
        dates: "",
        organization_personnel: "",
        department: "",
        theme: ""
      },
      personlist: [],
      workdepartments: [],
      initData: [],
      tableData: [
        {
          log_code: "112",
          dates: "123",
          department: "123",
          organization_personnel: "234",
          staff_code: "21334",
          theme: "234"
        },
        {
          log_code: "",
          dates: "123",
          department: "123",
          organization_personnel: "234",
          staff_code: "21334",
          theme: "234"
        }
      ],
      // 新增
      file: "",
      insertFlag: true,
      curSelectPerson: {},
      isShowErrogDialog: false,
      newFormData: {
        department: "",
        organization_personnel: "",
        theme: "",
        desc: ""
      },
      // changeDepartments: [],
      changeThemelist: ["主题一", "主题二"],
      log_title: "新增错误日志"
    };
  },
  methods: {
    // changedate() {
    //   if (this.form.dates === null) {
    //     console.log(this.form.dates);
    //     this.form.dates = [];
    //   }
    // },
    uploadFile() {
      this.file = this.$refs.uploadInput.files[0];
    },
    showDetail(rowItem) {
      this.insertFlag = false;
      this.isShowErrogDialog = true;
      this.curSelectPerson = rowItem;
      this.log_title = "详情查看";
    },
    query() {
      let formData = new FormData();
      for (let key in this.form) {
        if (this.form[key] && this.form[key].length) {
          formData.append(key, this.form[key]);
        }
      }
      console.log(this.form);
      let url = "server/personnel_management/error_log_data/";
      this.$axios.post(this._url2 + url, formData).then(res => {
        this.tableData = res.data.data;
      });
    },
    changeDepartment() {
      this.personlist = [];
      this.initData.forEach(i => {
        if (i.department === this.curSelectPerson.department) {
          this.personlist = i.organization_personnel;
          // console.log(this.personlist);
          // if (this.personlist.length) {
          //   this.curSelectPerson.organization_personnel = this.personlist[0];
          // }
        }
      });
    },
    selectDepartment() {
      console.log(111);
      this.personlist = [];
      this.initData.forEach(i => {
        if (i.department === this.form.department) {
          this.personlist = i.organization_personnel;
          // console.log(this.personlist);
          // if (this.personlist.length) {
          //   this.form.organization_personnel = this.personlist[0];
          // }
        }
      });
    },
    onSubmit() {
      console.log(this.curSelectPerson.theme, this.curSelectPerson.content);
      if (
        !this.curSelectPerson.department ||
        !this.curSelectPerson.organization_personnel ||
        !this.curSelectPerson.theme ||
        !this.curSelectPerson.content
      ) {
        alert("请选择部门，编制人，主题，内容");
        return;
      }
      let formData = new FormData();
      for (let key in this.curSelectPerson) {
        formData.append(key, this.curSelectPerson[key]);
      }
      if (this.file) {
        formData.append("file", this.file);
      }
      let url = "server/personnel_management/error_log_add/";
      this.$axios.post(this._url2 + url, formData).then(res => {
        if (res.data.code === 200) {
          alert("提交成功！");
          this.isShowErrogDialog = false;
          this.tableData = res.data.data; // 表格数据
        } else {
          alert("未提交成！");
        }
      });
    },
    newErrorLog() {
      this.curSelectPerson = {};
      this.insertFlag = true;
      this.isShowErrogDialog = true;
    },
    closeErrorLog() {
      this.isShowErrogDialog = false;
    },
    getSelect() {
      let url = "server/personnel_management/staff_error_log/";
      this.$axios.get(this._url2 + url).then(res => {
        console.log(res);
        this.initData = res.data.data || [];
        // 主题
        // this.changeThemelist = res.data.theme_list;
        this.initData.forEach(i => {
          this.workdepartments.push(i.department);
        });
      });
    },
    getTable() {
      let url = "server/personnel_management/error_log_data/";
      this.$axios.post(this._url2 + url).then(res => {
        this.tableData = res.data.data || [];
      });
    }
  },
  created() {
    this.getSelect();
    this.getTable();
  }
};
</script>

<style scoped>
.new_error_log {
  position: fixed;
  width: 440px;
  height: 600px;
  padding: 40px 60px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background: #fff;
  border: 1px solid;
  z-index: 999;
}
.error_log .table_con {
  padding: 20px 160px 30px;
  background: rgba(255, 255, 255, 0.9);
}
.error_log .table_con > h2 {
  margin-bottom: 30px;
}
.error_log .addNewBtn {
  position: absolute;
  top: 22px;
  left: 350px;
  margin: 0;
}
.error_log .table_con {
  position: relative;
}
.error_log a {
  color: blue;
}
.new_error_log h3 {
  margin: 0 0 40px;
  position: relative;
  text-align: center;
}
.error_log .topOpt {
  background: rgba(255, 255, 255, 0.9);
  padding: 30px 10px 10px;
  text-align: center;
  height: 90px;
}
.new_error_log .close_btn {
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 30px;
  cursor: pointer;
}
.new_error_log .uploadlink {
  position: relative;
  width: 70px;
  display: block;
}
.new_error_log .alink {
  display: block;
  width: 70px;
  text-align: center;
  height: 40px;
  cursor: pointer;
  color: blue;
}
.error_contain .fileInput {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
</style>
<style>
.new_error_log .el-textarea,
.new_error_log .el-button {
  width: 220px;
}
.error_log .table_con .el-button {
  /* margin: 40px 0 30px; */
}
</style>



